// ** Custom Components
import Avatar from '@components/avatar'

// ** Icons Imports
import { Calendar, Edit, PlusCircle, Trash2 } from 'react-feather'
import classnames from 'classnames'
// ** Reactstrap Imports
import {
  Card, CardTitle, CardBody, Modal, ModalBody, ModalHeader, Row, Col, Button, Label, Input, FormFeedback, Form, Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators
} from 'reactstrap'
import illustration from '@src/assets/images/illustration/email.svg'
import moment from 'moment'
import { Fragment, useState } from 'react'
import axios from '@src/configs/axios/axiosConfig'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { activeOrganization } from '@src/helper/sassHelper'
// const activeOrgId = activeOrganizationid()
const activeOrg = activeOrganization()
// const userId = orgUserId()
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import PerfectScrollbar from 'react-perfect-scrollbar'
const CardMeetup = () => {

  const schema = yup.object().shape({
    createdBy: yup.string().default(''),
    id: yup.string().default(''),
    organizationId: yup.string().default(''),
    name: yup.string().default('').required('Please Enter Name'),
    description: yup.string().default('').required('Please Enter Description')
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const [formModal, setFormModal] = useState(false)
  const [data, setData] = useState([])
  const addpopup = () => {
    setFormModal(true)
  }

  const getNotices = () => {
    axios.post('/noticeevents/list')
      .then((res) => {
        setData(res.data.noticeevents)
      })
  }

  const [selItem, setSelItem] = useState(null)

  const onSubmit = (obj) => {

    axios.post(`/noticeevents/${selItem ? 'update' : 'create'}`, obj)
      .then(() => {
        setFormModal(false)
        setValue('name', '')
        setValue('description', '')
        getNotices()
      })
  }

  const deleteItem = (id) => {
    axios.post(`/noticeevents/delete`, { id })
      .then(() => {
        getNotices()
      })
  }
  const deletefun = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await deleteItem(id)
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Notice has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }


  const editModal = (obj) => {
    setFormModal(true)
    setSelItem(obj)
    setValue('name', obj.name)
    setValue('id', obj.id)
    setValue('description', obj.description.replace(/(\\n)/g, "\n"))
  }

  const [activeIndex, setActiveIndex] = useState(0)
  const [animating, setAnimating] = useState(0)

  const onExiting = () => {
    setAnimating(true)
  }

  const onExited = () => {
    setAnimating(false)
  }

  const next = () => {
    if (animating) return
    const nextIndex = activeIndex === data.length - 1 ? 0 : activeIndex + 1
    setActiveIndex(nextIndex)
  }

  const previous = () => {
    if (animating) return
    const nextIndex = activeIndex === 0 ? data.length - 1 : activeIndex - 1
    setActiveIndex(nextIndex)
  }

  const goToIndex = newIndex => {
    if (animating) return
    setActiveIndex(newIndex)
  }

  useState(() => {
    getNotices()
  })

  const getRow = (fieldLabel, fieldName, reqflag = true, type) => {
    return (
      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input type={type} invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const closeModal = () => {
    setFormModal(false)
    setSelItem(null)
    setValue('id', '')
    setValue('name', '')
    setValue('description', '')
  }

  const slides = data.map(item => {
    return (
      <CarouselItem onExiting={onExiting} onExited={onExited} key={item.id} className='carousal-window scroll-example border-0'>
        <h5>{item.name}</h5>
        <p className='text-body white-space-wrap'>{item.description}</p>
        {(parseInt(activeOrg?.rolelevel) === 1 || parseInt(activeOrg?.rolelevel) === 2) &&
          <div className='carousal-action'>
            <Edit size={16} onClick={() => editModal(item)} className='text-primary me-1 cursor-pointer' />
            <Trash2 size={16} onClick={() => deletefun(item.id)} className='text-danger cursor-pointer' />
          </div>
        }
      </CarouselItem>
    )
  })

  return (
    <Card className='card-developer-meetup'>
      <div className='meetup-img-wrapper rounded-top text-center'>
        <img src={illustration} height='170' />
      </div>
      <CardBody>
        <div className='meetup-header d-flex align-items-center mb-1'>
          <div className='meetup-day'>
            <h6 className='mb-0'>{moment().format('ddd')}</h6>
            <h3 className='mb-0'>{moment().format('D')}</h3>
          </div>
          <div className='my-auto w-100'>
            <CardTitle className='mb-25 d-flex justify-content-between'>
              <h5>Notice Board</h5  >
              {(parseInt(activeOrg?.rolelevel) === 1 || parseInt(activeOrg?.rolelevel) === 2) &&
                <PlusCircle size={18} className='cursor-pointer' onClick={() => { addpopup() }} />
              }
            </CardTitle>
          </div>
        </div>
        <div className='mb-1 p-25 bg-light-primary'>
          <Carousel activeIndex={activeIndex} next={next} previous={previous}>
            <CarouselIndicators
              items={data}
              className='m-0'
              activeIndex={activeIndex}
              onClickHandler={goToIndex}
            />
            {slides}
          </Carousel>
        </div>
        < Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
          <ModalHeader toggle={() => closeModal()}>Notice Board</ModalHeader>
          <ModalBody>
            <Row className='gx-2 gy-1' tag={Form} onSubmit={handleSubmit(onSubmit)}>

              {getRow('Title', 'name', true, 'text')}
              {getRow('Message', 'description', true, 'textarea')}

              <Col className='mt-2 pt-1' xs={12}>
                <Button type='submit' className='me-1' color='primary'>
                  Submit
                </Button>
                <Button className='me-1' color='warning' onClick={() => closeModal()}>
                  Cancel
                </Button>
              </Col>

            </Row>
          </ModalBody>
        </Modal>

      </CardBody>
    </Card >
  )
}

export default CardMeetup
