// ** React Imports
import { useEffect, useState } from 'react'

// ** Third Party Components
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import {
  Row,
  Card,
  Table
} from 'reactstrap'


const RevenueReportTable = () => {
  // ** State
  const [data, setData] = useState([])

  const getData = () => {
    axios.post('/taskinvoices/revenuetable')
      .then(res => {
        setData(res.data.output || [])
      })
    return () => setData([])
  }

  const indianFormattedPrice = (val) => {


    const value = String(val)

    let removedValue = ''
    let finalVal = ''
    if (value.includes('.')) {
      removedValue = value.slice(value.indexOf('.'))
      finalVal = value.replace(removedValue, '')
      finalVal = finalVal.toString()
    } else {
      finalVal = value.toString()
    }
    let lastThree = finalVal.substring(finalVal.length - 3)
    const otherNumbers = finalVal.substring(0, finalVal.length - 3)
    if (otherNumbers !== '') {
      lastThree = `,${lastThree}`
    }
    const res = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree
    return res
  }

  useEffect(() => {

    getData()

  }, [])

  return (
    <Card className='card-revenue-budget'>
      <Row className='mx-0'>
        <Table className='table' responsive>
          <thead>
            <tr>
              <th></th>
              <th>Sales</th>
              <th>Receipts</th>
              <th>Due</th>
            </tr>
          </thead>
          <tbody>
            {
              data.map((obj, k) => {
                return (
                  <tr key={k}>
                    <td style={{ padding: '12px 10px', fontSize: '13px' }}>{obj.date}</td>
                    <td style={{ padding: '12px 10px', fontSize: '13px' }} >{indianFormattedPrice(obj.sales)}</td>
                    <td style={{ padding: '12px 10px', fontSize: '13px' }}>{indianFormattedPrice(obj.receipts)}</td>
                    <td style={{ padding: '12px 10px', fontSize: '13px' }}>{indianFormattedPrice(obj.due)}</td>
                  </tr>
                )
              })
            }
          </tbody>
        </Table>
      </Row>
    </Card>
  )
}

export default RevenueReportTable
