// ** Reactstrap Imports
import { Row, Col, Card, CardBody, CardHeader, Button } from 'reactstrap'
// ** Context
//import { ThemeColors } from '@src/utility/context/ThemeColors'

// ** Demo Components
import CardAttendance from '../../ui-elements/cards/advance/CardAttendance'
import StatsCard from '@src/views/ui-elements/cards/statistics/StatsCard'
import MyTaskTodays from '@src/views/ui-elements/cards/advance/MyTaskTodays'
import MyTaskDueTodays from '@src/views/ui-elements/cards/advance/MyTaskDueTodays'
import MyOverdueTasks from '@src/views/ui-elements/cards/advance/MyOverdueTasks'
import CardMeetup from '@src/views/ui-elements/cards/advance/CardMeetup'
import CardEmployeesOpenTask from '../../ui-elements/cards/advance/CardEmployeesOpenTask'
import RevenueReport from '../../ui-elements/cards/analytics/RevenueReport'
import RevenueReportTable from '../../ui-elements/cards/analytics/RevenueReportTable'

import { ThemeColors } from '@src/utility/context/ThemeColors'
// ** Styles
import axios from '@src/configs/axios/axiosConfig'
import '@styles/react/libs/charts/apex-charts.scss'
import '@styles/base/pages/dashboard-ecommerce.scss'
import { Fragment, useContext, useEffect, useState } from 'react'
import { activeOrganization } from '@src/helper/sassHelper'
import { CheckCircle } from 'react-feather'
import { Link } from 'react-router-dom'

const activeOrg = activeOrganization()

const EcommerceDashboard = () => {

  const context = useContext(ThemeColors)
  const [settingFlags, setSettingFlags] = useState(null)

  const getSettingFlag = () => {
    axios.post('/organizationprefernces/getsettingsflag')
      .then((res) => {
        setSettingFlags(res.data.result)
      }).catch((err) => {
        console.log(err)
      })
  }

  const settingFlagFn = () => {
    if (settingFlags !== null) {
      return (settingFlags['drive_flag'] || settingFlags['gmail_flag'] || settingFlags['attendance_flag'] || settingFlags['leave_flag'] || settingFlags['invoicesetting_flag'])
    } else {
      return false
    }
  }
  const dashboardFlag = () => {
    return (parseInt(activeOrg?.rolelevel) === 1 || parseInt(activeOrg?.rolelevel) === 2)
  }
  useEffect(async () => {
    await getSettingFlag()
  }, [])

  return (
    <div id='dashboard-ecommerce'>
      <Row className='match-height'>
        {dashboardFlag() &&
          <Col xl={dashboardFlag() ? '4' : '6'} md={dashboardFlag() ? '4' : '6'} xs='12' className='d-flex'>
            <Row>
              <Col md={12}>
                <CardAttendance margin='mt-2' padding='' />
              </Col>
              <Col md={12}>
                {!settingFlagFn() &&
                  <StatsCard className='py-0' cols={{ xl: dashboardFlag() ? '6' : '6', sm: '6' }} />
                }
                {(settingFlagFn()) &&

                  <Card>
                    <CardHeader>Settings</CardHeader>
                    <CardBody>
                      <Col className='py-50'>
                        <CheckCircle size={15} className={settingFlags['drive_flag'] ? 'text-danger' : 'text-success'} />
                        <Col className='ms-75' tag={Link} to={`/settings/integrations`}>Link Google Drive</Col>
                      </Col>
                      <Col className='py-50'> <CheckCircle size={15} className={settingFlags['gmail_flag'] ? 'text-danger' : 'text-success'} />
                        <Col className='ms-75' tag={Link} to={`/settings/integrations`}>Link GMail</Col></Col>
                      <Col className='py-50'>
                        <CheckCircle size={15} className={settingFlags['invoicesetting_flag'] ? 'text-danger' : 'text-success'} />
                        <Col className='ms-75' tag={Link} to={`/invoicesetting/invoiceaccount`}>Invoice Setting</Col>
                      </Col>
                      <Col className='py-50'><CheckCircle size={15} className={settingFlags['attendance_flag'] ? 'text-danger' : 'text-success'} />
                        <Col className='ms-75' tag={Link} to={`/attendancesetting/attendancescore`}>Attendance Setting</Col>
                      </Col>
                      <Col className='py-50'>
                        <CheckCircle size={15} className={settingFlags['leave_flag'] ? 'text-danger' : 'text-success'} />
                        <Col className='ms-75' tag={Link} to={`/attendancesetting/leavesetting`}>Leave Setting</Col>
                      </Col>
                    </CardBody>
                  </Card>

                }
              </Col>
            </Row>
          </Col>
        }
        {
          !dashboardFlag() &&
          <Col xl={12} md={12} xs='12' className='d-flex'>
            <Row className='d-flex match-height'>
              <Col md={6}>
                <CardAttendance margin='mt-4' />
                <StatsCard className='py-0' cols={{ xl: '6', sm: '6' }} />
              </Col>
              < Col md={6}>
                <CardMeetup />
              </Col>
            </Row>
          </Col>
        }

        {
          dashboardFlag() &&
          <Fragment>
            <Col xl='4' md='4' xs='12'>
              <CardEmployeesOpenTask />
            </Col>
            <Col xl='4' md='4' xs='12'>
              <CardMeetup />
            </Col>
          </Fragment>
        }

      </Row >
      {
        parseInt(activeOrg?.rolelevel) === 1 &&
        <Row>
          <Col md={4}>
            <RevenueReportTable />
          </Col>
          <Col md={8}>
            <RevenueReport success={context.colors.success.main} primary={context.colors.primary.main} />
          </Col>
        </Row>
      }
      < Row className='match-height' >
        <Col xl='4' md='6' xs='12'>
          <MyOverdueTasks cols={{ xl: '2', sm: '6' }} />
        </Col>
        <Col xl='4' md='6' xs='12'>
          <MyTaskDueTodays cols={{ xl: '2', sm: '6' }} />
        </Col>
        <Col xl='4' md='6' xs='12'>
          <MyTaskTodays />
        </Col>
      </Row >
    </div >
  )
}

export default EcommerceDashboard
